<template>
    <div class="container">
        <div class="main__posts">
            <div v-for="post in posts" :key="post.id" class="main__posts-item">
                <h3 class="main__posts-title">{{ post.title }}</h3>
                <p class="main__posts-text">{{ post.body }}</p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        created() {
            this.getPosts()
        },
        computed: {
            posts() {
                return this.$store.state.posts
            }
        },
        methods: {
            getPosts() {
                this.$store.dispatch('getPosts')
            }
        }
    }
</script>